.container{
    width: 70%;
    
    margin-left: 15%;
    margin-right: 20px;
    margin-top: 20px;
    padding-bottom: 20px;
    
}
.App{
    background-color: dimgray;
    
}

.padd{
    padding: 15px;
}

.search-field{
    width: 80%;
    height: 40px;
    font-size: 20px;
    border-radius: 10px;
    border: 1px solid gray;
    outline: none;
    text-indent: 20px;
    background-color: dimgray;
    
    

}

.movie{
    padding: 5px;
   /* background-color: gray;*/
    margin-top: 30px;
    display: flex;
    justify-content: space-between;

}

.poster{
    height: 600px;
    width: 33%;
    /*background-color: red;*/
    overflow: hidden;
}

.details{
    
    width: 65%;
   /* background-color: green;*/
}
.img-poster{
    width: 100%;
    
    height: 100%;
}
.padd p{
    font-size: 20px;
}

h1,h2,h3{
    margin: 0px;
}

.ratings{
    display: flex;
    
}

.input-group{
    display: flex;
    margin-top: 10px;
    justify-content: space-around;
    
}

.btn{
    width: 15%;
    background-color: black;
    color: blanchedalmond;
}

.ratings>div{
    margin-right: 20px;
}